.campo {
  margin: 24px 0;
  font-family: "Montserrat", sans-serif;
}

.campo label {
  font-size: 18px;
  font-weight: 600;
  display: block;
  margin-bottom: 8px;
}

.campo input,
.campo select {
  width: 100%;
  background-color: #ffffff;
  box-shadow: 10px 10px 20px rgb(0 0 0 / 0.3);
  border: none;
  padding: 15px 20px;
  outline-color: #6278f7;
  font-size: 16px;
  border-radius: 7px;
  box-sizing: border-box;
}

.campo-color input {
  height: 40px;
  padding: 5px 20px;
  width: 50%;
}
