.formulario {
  display: flex;
  justify-content: space-evenly;
  gap: 20px;
  flex-wrap: wrap;
  margin: 80px 50px;
}

.formulario form {
  flex: 1;
  background-color: #f7f7f7;
  border-radius: 20px;
  box-shadow: 7px 7px 7px rgb(0 0 0 / 0.4);
  padding: 30px 20px;
  margin: 80px auto;
  box-sizing: border-box;
}

@media (min-width: 768px) {
  .formulario form {
    padding: 40px 100px;
  }
}

.formulario h2 {
  font-size: 19px;
  font-weight: bold;
  color: #212121;
  display: inline;
}

@media (min-width: 768px) {
  .formulario h2 {
    font-size: 32px;
  }
}
