.orgSection {
  width: 100%;
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.orgSection img {
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}
.orgSection img:hover {
  transform: scale(1.04);
}

.title {
  color: #6278f7;
  font-size: 23px;
  font-weight: 400;
  padding-bottom: 24px;
  border-bottom: 4px solid #6278f7;
}

@media (min-width: 480px) {
  .title {
    font-size: 30px;
  }
}

@media (min-width: 768px) {
  .title {
    font-size: 40px;
  }
}
