.footer {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  align-items: center;
  padding: 5%;
  box-sizing: border-box;
  background-size: cover;
  background-position: center;
  justify-content: space-between;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
}

@media (min-width: 768px) {
  .footer {
    display: flex;
    font-size: 20px;
  }
}
.redes {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
